	module.exports = {"data":{"site":{"siteMetadata":{"docSections":{"basics":["/basics/introduction/"],"guides":["/guides/quick-start-guide/"],"configurations":["/configurations/overview/"],"testing":["/testing/manual-testing/"]}}},"allMarkdownRemark":{"edges":[{"node":{"fields":{"slug":"/404/"},"frontmatter":{"title":"","id":null}}},{"node":{"fields":{"slug":"/basics/introduction/"},"frontmatter":{"title":"Introduction","id":"introduction"}}},{"node":{"fields":{"slug":"/configurations/overview/"},"frontmatter":{"title":"Configuration overview","id":"overview"}}},{"node":{"fields":{"slug":"/guides/quick-start-guide/"},"frontmatter":{"title":"Quick Start Guide","id":"quick-start-guide"}}},{"node":{"fields":{"slug":"/testing/manual-testing/"},"frontmatter":{"title":"Manual Testing","id":"manual-testing"}}}]},"markdownRemark":{"html":"<p>Storybook supports many different frontend view layers with more coming!\nReact, Vue, Angular, Mithril, Marko, HTML, Svelte, Meteor, Ember, Riot and Preact are currently supported. Follow these steps to get started with Storybook.</p>\n<p>Get started using the automated command line tool. This command adds a set of boilerplate files for Storybook in your project:</p>\n<pre><code class=\"language-sh\">cd my-project-directory\nnpx -p @storybook/cli sb init\n</code></pre>\n<p>The tool inspects your <code>package.json</code> to determine which view layer you’re using. If you want to develop HTML snippets in storybook, we can’t determine that automatically. So to install storybook for HTML, use the <code>--type</code> flag to force that the HTML project type is set:</p>\n<pre><code class=\"language-sh\">npx -p @storybook/cli sb init --type html\n</code></pre>\n<p>It’s also useful if our automatic detection fails.</p>\n<p>By default npx will use the latest version, if you want to try out the next version (or any specific version), you can use the following:</p>\n<pre><code class=\"language-sh\">npx -p @storybook/cli@5.0.0-rc.6 sb init\n</code></pre>\n<p>To setup a project manually, take a look at the <a href=\"/docs/guides/slow-start-guide/\">Slow Start Guide</a>.</p>\n<p>Start Storybook with:</p>\n<pre><code class=\"language-sh\">npm run storybook\n</code></pre>\n<p>Storybook should now be available in the browser with a link provided in the console.</p>\n<hr>\n<p>To learn more about what the Storybook CLI command <code>sb init</code> command does, have a look at the slow start guides:</p>\n<ul>\n<li><a href=\"/docs/guides/guide-react/\">React</a></li>\n<li><a href=\"/docs/guides/guide-react-native/\">React Native</a></li>\n<li><a href=\"/docs/guides/guide-vue/\">Vue</a></li>\n<li><a href=\"/docs/guides/guide-angular/\">Angular</a></li>\n<li><a href=\"/docs/guides/guide-mithril/\">Mithril</a></li>\n<li><a href=\"/docs/guides/guide-marko/\">Marko</a></li>\n<li><a href=\"/docs/guides/guide-html/\">HTML</a></li>\n<li><a href=\"/docs/guides/guide-svelte/\">Svelte</a></li>\n<li><a href=\"/docs/guides/guide-ember/\">Ember</a></li>\n<li><a href=\"/docs/guides/guide-riot/\">Riot</a></li>\n<li><a href=\"/docs/guides/guide-preact/\">Preact</a></li>\n</ul>\n<p>A step-by-step tutorial is available at <a href=\"https://www.learnstorybook.com\">Learn Storybook</a>.</p>","fields":{"slug":"/guides/quick-start-guide/"},"frontmatter":{"title":"Quick Start Guide","id":"quick-start-guide"}}},"pathContext":{"slug":"/guides/quick-start-guide/"}}

